import React, { useRef, useState } from 'react';
import { Text, Group, Button, useMantineTheme, LoadingOverlay} from '@mantine/core';
import { Dropzone } from '@mantine/dropzone';
import { IconCloudUpload, IconX, IconRefresh, IconDownload } from '@tabler/icons-react';
import styles from './DropzoneButton.module.css';

import dayjs from 'dayjs';
import utc from 'dayjs/plugin/utc';
import timezone from 'dayjs/plugin/timezone';
import duration from 'dayjs/plugin/duration';

dayjs.extend(utc);
dayjs.extend(timezone);
dayjs.extend(duration);

function DropzoneButton() {
  const theme = useMantineTheme();
  const openRef = useRef();
  const [files, setFiles] = useState([]);
  const [loading, setLoading] = useState(false);

  const handleDrop = (acceptedFiles) => {
    setFiles(currentFiles => [...currentFiles, ...acceptedFiles]);
  };


  function parseTime(timeStr) {
    if (!timeStr || timeStr.toUpperCase() === 'NULL') {
      return 0; // Возвращаем 0, если время отсутствует или NULL
    }
  
    let totalSeconds = 0;
    const parts = timeStr.split(':');
    const secondsParts = parts[parts.length - 1].split('.');
  
    // Обрабатываем основные части времени (часы, минуты)
    if (parts.length === 3) {
      totalSeconds += parseInt(parts[0]) * 3600; // часы
      totalSeconds += parseInt(parts[1]) * 60;   // минуты
    } else if (parts.length === 2) {
      totalSeconds += parseInt(parts[0]) * 60;   // минуты
    }
  
    // Добавляем секунды
    totalSeconds += parseInt(secondsParts[0]);  // секунды
  
    // Добавляем миллисекунды, делённые на 1000, для преобразования в секунды
    if (secondsParts.length > 1) {
      totalSeconds += parseFloat('0.' + secondsParts[1]);
    }
  
    return totalSeconds;
  }
  
  function formatTime(seconds) {
    // Округляем секунды до ближайшего целого числа для исключения миллисекунд
    const roundedSeconds = Math.round(seconds);
    const hours = Math.floor(roundedSeconds / 3600);
    const minutes = Math.floor((roundedSeconds % 3600) / 60);
    const secs = roundedSeconds % 60;
  
    return [hours, minutes, secs].map(v => v.toString().padStart(2, '0')).join(':');
  }
  
  // Функция для выбора самого частого месяца и года
  function getMostFrequent(counter) {
    return Object.entries(counter).reduce((a, b) => (b[1] > a[1] ? b : a))[0];
  }
  
  // Функция для преобразования номера месяца в название месяца
  function getMonthName(monthNumber) {
    const monthNames = [
      "Январь", "Февраль", "Март", "Апрель", "Май", "Июнь",
      "Июль", "Август", "Сентябрь", "Октябрь", "Ноябрь", "Декабрь"
    ];
    return monthNames[monthNumber];
  }

  
  
  


  const handleTransform = async () => {
    if (files.length < 2) {
      alert("Please upload at least two JSON files for merging.");
      return;
    }
  
    setLoading(true);
  
    const dataPromises = files.map(file => readFileAsJSON(file));
    
    Promise.all(dataPromises)
      .then(results => {
        const mergedData = results.flat(); // Объединяем данные из файлов
        processMergedData(mergedData); // Начинаем обработку данных
      })
      .catch(error => {
        console.error("Error processing files:", error);
      })
      .finally(() => setLoading(false));
  };
  
  // Чтение файла и его парсинг
  const readFileAsJSON = (file) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onload = () => {
        try {
          resolve(JSON.parse(reader.result));
        } catch (e) {
          reject(e);
        }
      };
      reader.onerror = () => reject(reader.error);
      reader.readAsText(file);
    });
  };
  
// Основная функция обработки данных
const processMergedData = (mergedData) => {
  const statAuditlog = {};
  const statTimetracker = {};
  const dailyWork = {}; // Объединяем данные о времени работы для всех пользователей

  mergedData.forEach(item => {
    const seconds = parseTime(item.time_spent);
    if (!item.start_time) return;

    const formattedDate = dayjs.utc(item.start_time).tz('Europe/Kiev').format('YYYY-MM-DD');
    console.log(`Formatted date: ${formattedDate}`);

    console.log(`Parsed time spent: ${seconds} seconds`);

    switch (item.source_table) {
      case 'auditlog':
        // Обработка данных из auditlog
        const auditStats = statAuditlog[item.user_name] = statAuditlog[item.user_name] || {
          task_count: 0, task_time: 0, complete: 0, not_complete: 0, missed: 0, closed_by_timeout: 0, not_closed: 0,
          not_complete_tasks: {
            PreFilterPictures: 0,
            ImageChoice: 0,
            ImageBackgroundRemoval: 0,
            ProductModeration: 0,
            DoubtfulModeration: 0,
            ProductApproval: 0,
            UpdatePictureFilter: 0,
            ImageUpdate: 0,
            ProductUpdate: 0
          },
          monthCounter: {},
          yearCounter: {}
        };

        auditStats.task_count += 1;
        auditStats.task_time += seconds;

        if (item.is_complete === "true") {
          auditStats.complete += 1;
        } else {
          auditStats.not_complete += 1;
          if (auditStats.not_complete_tasks[item.task_type] !== undefined) {
            auditStats.not_complete_tasks[item.task_type] += 1;
          }
        }

        if (item.is_missed === "true") {
          auditStats.missed += 1;
        }

        if (item.finish_note === "Closed by timeout") {
          auditStats.closed_by_timeout += 1;
        } else if (item.finish_note === "Not Closed") {
          auditStats.not_closed += 1;
        }

        // Добавляем время для расчета отработанных часов по дням
        const auditDate = dayjs.utc(item.start_time).tz('Europe/Kiev').format('YYYY-MM-DD');
        const [year, month] = auditDate.split('-');
        dailyWork[item.user_name] = dailyWork[item.user_name] || {};
        dailyWork[item.user_name][auditDate] = dailyWork[item.user_name][auditDate] || {
          totalSeconds: 0,
          auditSeconds: 0,
          customSeconds: 0,
          complexSeconds: 0
        };
        dailyWork[item.user_name][auditDate].totalSeconds += seconds;
        dailyWork[item.user_name][auditDate].auditSeconds += seconds; // Добавляем в audit

        console.log(`Updated dailyWork for ${item.user_name} on ${auditDate}:`, dailyWork[item.user_name][auditDate]);

        // Считаем месяцы и годы
        auditStats.monthCounter[month] = (auditStats.monthCounter[month] || 0) + 1;
        auditStats.yearCounter[year] = (auditStats.yearCounter[year] || 0) + 1;

        break;

      case 'timetracker':
        // Обработка данных из timetracker
        const trackerStats = statTimetracker[item.user_name] = statTimetracker[item.user_name] || {
          task_count: 0, task_custom_count: 0, task_complex_count: 0, custom_time: 0, complex_time: 0, task_time: 0,
          complete: 0, not_complete: 0, missed: 0
        };

        trackerStats.task_count += 1;

        const trackerDate = dayjs.utc(item.start_time).tz('Europe/Kiev').format('YYYY-MM-DD');
        dailyWork[item.user_name] = dailyWork[item.user_name] || {};
        dailyWork[item.user_name][trackerDate] = dailyWork[item.user_name][trackerDate] || {
          totalSeconds: 0,
          auditSeconds: 0,
          customSeconds: 0,
          complexSeconds: 0
        };

        if (item.task_type === 'Custom') {
          trackerStats.task_custom_count += 1;
          trackerStats.custom_time += seconds;
          dailyWork[item.user_name][trackerDate].totalSeconds += seconds;
          dailyWork[item.user_name][trackerDate].customSeconds += seconds; // Добавляем в custom
        } else if (item.task_type === 'Complex') {
          trackerStats.task_complex_count += 1;
          trackerStats.complex_time += seconds;
          dailyWork[item.user_name][trackerDate].totalSeconds += seconds;
          dailyWork[item.user_name][trackerDate].complexSeconds += seconds; // Добавляем в complex
        } else {
          trackerStats.task_time += seconds;
          dailyWork[item.user_name][trackerDate].totalSeconds += seconds;
        }

        if (item.is_complete === "true") {
          trackerStats.complete += 1;
        } else {
          trackerStats.not_complete += 1;
        }

        if (item.is_missed === "true") {
          trackerStats.missed += 1;
        }

        break;

      default:
        console.warn(`Неизвестный source_table: ${item.source_table}`);
    }
  });

  const finalData = formatFinalData(mergedData, statAuditlog, statTimetracker, dailyWork);
  console.log('Final processed data:', finalData);

  const fileName = generateFileName(mergedData);
  downloadJson(JSON.stringify(finalData, null, 2), `${fileName}.json`);
};

// Универсальная функция для получения всех уникальных дат из mergedData
const generateAllDatesFromMergedData = (mergedData) => {
  const allDates = new Set();
  mergedData.forEach(item => {
    if (item.start_time) {
      const formattedDate = dayjs.utc(item.start_time).tz('Europe/Kiev').format('YYYY-MM-DD');
      allDates.add(formattedDate);
    }
  });
  return Array.from(allDates).sort(); // Возвращаем отсортированный массив уникальных дат
};

// Подготовка финальных данных с использованием фактических дат из mergedData
const formatFinalData = (mergedData, statAuditlog, statTimetracker, dailyWork) => {
  const finalData = [...mergedData];
  const underworkedThresholdSeconds = (4 * 3600) + (45 * 60); // 4 часа и 45 минут в секундах

  // Добавляем статистику auditlog
  Object.entries(statAuditlog).forEach(([user_name, data]) => {
    data.not_complete_percentage = ((data.not_complete / data.task_count) * 100).toFixed(2);
    const mostFrequentMonth = getMonthName(Number(getMostFrequent(data.monthCounter)) - 1);
    const mostFrequentYear = getMostFrequent(data.yearCounter);

    finalData.push({
      source_table: "stat_auditlog",
      user_name,
      ...data,
      task_time: formatTime(data.task_time),
      month: mostFrequentMonth,
      year: mostFrequentYear
    });
  });

  // Добавляем статистику timetracker
  Object.entries(statTimetracker).forEach(([user_name, data]) => {
    data.not_complete_percentage = ((data.not_complete / data.task_count) * 100).toFixed(2);
    finalData.push({
      source_table: "stat_timetracker",
      user_name,
      ...data,
      custom_time: formatTime(data.custom_time),
      complex_time: formatTime(data.complex_time),
      task_time: formatTime(data.task_time)
    });
  });

  // Генерируем уникальные даты на основе данных
  const allDates = generateAllDatesFromMergedData(mergedData);

  // Добавляем отработанные часы для всех пользователей
  Object.entries(dailyWork).forEach(([user_name, dates]) => {
    console.log(`Processing worked_hours for user: ${user_name}`);
    console.log('Dates data:', dates);

    const workedDays = allDates.map(date => {
      const data = dates[date] || { totalSeconds: 0, auditSeconds: 0, customSeconds: 0, complexSeconds: 0 };
      console.log(`Worked data for date ${date}:`, data);

      const underworked = data.totalSeconds < underworkedThresholdSeconds;

      return {
        date,
        hours_worked: formatTime(data.totalSeconds),
        auditHours: formatTime(data.auditSeconds),
        customHours: formatTime(data.customSeconds),
        complexHours: formatTime(data.complexSeconds),
        underworked: underworked ? "true" : "false"
      };
    });

    console.log('Worked days:', workedDays);

    finalData.push({
      source_table: "worked_hours",
      user_name,
      worked_days: workedDays
    });
  });

  return finalData;
};

// Генерация имени файла
const generateFileName = (mergedData) => {
  const allDates = mergedData.map(item => dayjs.utc(item.start_time).tz('Europe/Kiev'));
  const minDate = allDates.reduce((a, b) => a.isBefore(b) ? a : b);
  const maxDate = allDates.reduce((a, b) => a.isAfter(b) ? a : b);

  const period = `${minDate.date()}-${maxDate.date()}`; // Используем диапазон реальных дат

  return `${minDate.year()}-${minDate.format('MMM').toUpperCase()}_${period}`;
};

// Функция для скачивания файла
const downloadJson = (jsonString, fileName) => {
  const blob = new Blob([jsonString], { type: 'application/json' });
  const url = URL.createObjectURL(blob);
  const link = document.createElement('a');
  link.href = url;
  link.download = fileName;
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
  URL.revokeObjectURL(url);
};

  


  

  


  const handleReset = () => {
    setFiles([]); // Очищаем массив файлов
  };

  return (
    <div className={styles.wrapper}>
      <LoadingOverlay visible={loading} />
      <Dropzone
        openRef={openRef}
        onDrop={handleDrop}
        className={styles.dropzone}
        radius="md"
        accept={['application/json']} // Принимаем только JSON файлы
        maxSize={30 * 1024 ** 2} // Максимальный размер файла 30MB
        multiple  // Разрешение на загрузку нескольких файлов
      >
        <div style={{ pointerEvents: 'none' }}>
          <Group justify="center">
            <Dropzone.Accept>
              <IconDownload
                style={{ width: 50, height: 50 }}
                color={theme.colors.blue[6]}
                stroke={1.5}
              />
            </Dropzone.Accept>
            <Dropzone.Reject>
              <IconX
                style={{ width: 50, height: 50 }}
                color={theme.colors.red[6]}
                stroke={1.5}
              />
            </Dropzone.Reject>
            <Dropzone.Idle>
              <IconCloudUpload
                style={{ width: 50, height: 50 }}
                stroke={1.5}
              />
            </Dropzone.Idle>
          </Group>

          <Text ta="center" fw={700} fz="lg" mt="xl">
            <Dropzone.Accept>Drop files here</Dropzone.Accept>
            <Dropzone.Reject>Only JSON files less than 30MB</Dropzone.Reject>
            <Dropzone.Idle>Upload JSON files</Dropzone.Idle>
          </Text>
          <Text ta="center" fz="sm" mt="xs" c="dimmed">
            Drag'n'drop files here, or click to select files.
          </Text>
        </div>
      </Dropzone>

      {files.length > 0 && (
        <div className={styles.fileList}>
          {files.map((file, index) => (
            <Text className={styles.file_badge} key={index}>{file.name}</Text>
          ))}
        </div>
      )}
      <div className={styles.controls_button}>
        <Button
          className={styles.control}
          size="md"
          radius="xl"
          onClick={handleTransform}
        >
          Download
        </Button>
        <Button
          className={styles.control}
          size="md"
          radius="xl"
          color="red"
          onClick={handleReset}
        ><IconRefresh stroke={2} />
        </Button>
      </div>
    </div>
  );
}

export default DropzoneButton;
